import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { GlobalService } from './global.service';
import { JsonFormData, SpecialServiceDetialDTO } from '../models/special-services.model';

@Injectable({
  providedIn: 'root'
})
export class SpecialServicesService {

  constructor(private http: HttpClient, private gs: GlobalService) {

  }
  getData(carrierIntegrationID: string, carrierCode: string): Promise<SpecialServiceDetialDTO[]> {
    return this.http.get<SpecialServiceDetialDTO[]>(this.gs.baseURL + 'api/SpecialServiceDetail',{
        params: {
            CarrierIntegrationID : carrierIntegrationID,
            CarrierCode: carrierCode
        }
    }).toPromise();
  }

  
}
