import { Component, OnInit } from "@angular/core";
import { Router } from "@angular/router";
import { BoxTypeConfigurationService } from "src/app/services/boxTypeConfiguration.service";
import { DataService } from "src/app/services/data.service";
import { ProcessShipperService } from "src/app/services/process-shipper.service";

@Component({
  selector: "app-box-type-configurations",
  templateUrl: "./box-type-configurations.component.html",
  styleUrls: ["./box-type-configurations.component.scss"],
})
export class BoxTypeConfigurationsComponent implements OnInit {
  data: any;
  columns = [
    "ID",
    "Code",
    "Description",
    "Type",
    "Length",
    "Width",
    "Height",
    "Cost",
    "Active",
    "Plant ID",
  ];
  constructor(
    private boxTypeConfigService: BoxTypeConfigurationService,
    private dataService: DataService,
    private router: Router
  ) {}

  ngOnInit(): void {
    this.boxTypeConfigService.get().subscribe((plantData) => {
      console.log(plantData);
      this.data = plantData;
    });
  }
  createBoxType() {
    this.router.navigate(["create-boxType-configuration"]);
  }
  edit(id) {
    this.router.navigate(["create-boxType-configuration/" + id]);
  }
}
