import { Byte } from '@angular/compiler/src/util';
import { Component, OnInit, Renderer2 } from '@angular/core';
import { DomSanitizer, SafeUrl } from '@angular/platform-browser';
import { ActivatedRoute, Router } from '@angular/router';


@Component({
  selector: 'app-pop-up',
  templateUrl: './pop-up.component.html',
  styleUrls: ['./pop-up.component.scss']
})
export class PopUpComponent implements OnInit {
  image: SafeUrl;
  labelData: string;
  constructor(private sanitizer: DomSanitizer, private route: ActivatedRoute, private renderer: Renderer2) { }

  ngOnInit(): void {
    this.route.queryParams.subscribe(e => {
      this.labelData = localStorage.getItem(e['HandlingUnitID']);
      let objectURL = 'data:image/png;base64,' + this.labelData;
      this.image = this.sanitizer.bypassSecurityTrustUrl(objectURL);
      localStorage.removeItem(e['HandlingUnitID']);
      const degree = 90;
      const image = document.getElementById('image');
      this.renderer.setStyle(
        image,
        'transform',
        `rotate(${degree}deg)`
      )

    })




  }

}
