import { Component, Input, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { DataService } from 'src/app/services/data.service';
import { ProcessShipperService } from 'src/app/services/process-shipper.service';

@Component({
  selector: 'app-plant-configuration-list',
  templateUrl: './plant-configuration-list.component.html',
  styleUrls: ['./plant-configuration-list.component.scss']
})
export class PlantConfigurationListComponent implements OnInit {



  @Input() data =[]

  @Input() columns = ["Plant ID","Contact Name","City","State","Country"]
  constructor(private processShipperService: ProcessShipperService,
    private dataService: DataService,private router: Router) { }

  ngOnInit(): void {
    this.processShipperService.getPlantConfigurationData().subscribe(plantData=>{
      console.log(plantData);
      this.data = plantData;
  });
}
edit(id){
  this.router.navigate(['plants/update/'+id]);
}
create(){
  this.router.navigate(['plants/create/']);
}


}
