import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-bol-configuration',
  templateUrl: './bol-configuration.component.html',
  styleUrls: ['./bol-configuration.component.scss']
})
export class BolConfigurationComponent implements OnInit {

  administrationList = false;
  batchProcessList = false;
  configurationList = true;
  ERPSetupList = false;
  hazardousList = false;
  carriersIntegrationList = false;
  carriersNonIntegrationList = false;
  toolKit = false;
  dominonDetails = true;

  showAddBOLConfigurationPopup = false;
  columns = [
    "Quick Actions",
    "Plant ID",
    "Print Customer Info",
    "Footer Text",
    "Header Text",
    "Left Image",
    "Right Image",
  ];
  router: any;
  data: any;
  svcService: any;

  ngOnInit(): void {
    this.svcService.get().subscribe((data) => {
      this.data = data;
    });
  }
  edit(id) {
    this.router.navigate(["create-ship-via-code-configuration/" + id]);
  }
  create() {
    this.router.navigate(["create-address-master"]);
  }
}
