import { Component, Input, OnInit } from "@angular/core";

@Component({
  selector: "app-preview",
  templateUrl: "./preview.component.html",
  styleUrls: ["./preview.component.scss"],
})
export class PreviewComponent implements OnInit {
  columns = ["Quick Actions", "Key", "Value", "Answer"];
  showPopup = false;
  router: any;
  @Input() data: any;
  svcService: any;
  constructor() {}

  ngOnInit(): void {}

  edit(id) {
    this.router.navigate(["create-ship-via-code-configuration/" + id]);
  }
}
