import { ChangeDetectionStrategy, Component, Input, OnChanges, Output, SimpleChanges, EventEmitter } from '@angular/core';
import { FormArray, FormBuilder, FormControl, FormGroup, ReactiveFormsModule } from '@angular/forms';
import { SpecialServicesService } from 'src/app/services/specialService.service';
import { JsonFormData, SpecialServiceDetialDTO, SpeicalServiceSelectedData } from '../../models/special-services.model';


@Component({
  selector: 'app-special-services',
  templateUrl: './special-services.component.html',
  styleUrls: ['./special-services.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class SpecialServicesComponent implements OnChanges {
  myForm: FormGroup;
  @Input() jsonFormData: SpecialServiceDetialDTO[];
  @Output() closePopup = new EventEmitter();

  selectedData: SpeicalServiceSelectedData = {};

  constructor(private fb: FormBuilder) {
    // this.myForm = this.fb.group({});
  }


  ngOnChanges(changes: SimpleChanges): void {
    if (!changes.jsonFormData.firstChange) {

      let group = {};
      for (const control of this.jsonFormData) {
        group[control.specialServiceName] = new FormControl(control.value);
      }
      //this.createForm(this.jsonFormData);
      this.myForm = new FormGroup(group);
    }

  }

  // createForm(controls: SpecialServiceDetialDTO[]) {
  //   if (controls) {
  //     for (const control of controls) {
  //      this.myForm.addControl(control.specialServiceName, this.fb.control(''))
  //       //(<FormArray>this.FormData.get('specialService')).push(this.getSpecialService());
  //     }
  //     //this.FormData = new FormGroup(form);
  //   }

  // }

  checkBoxChanged(event, index) {

    if (event.checked) {
      this.jsonFormData[index].selectedValue = "Yes";
    }
    else {
      this.jsonFormData[index].selectedValue = "No";
    }

  }
  SaveSpecialService() {
    var obj = this.myForm.value;

    let data = {};
    let displayData = {};
    Object.keys(obj).forEach(element => {
      if (obj[element] != null && obj[element].toString().length > 0)
        data[element] = obj[element];
    });

    
    this.jsonFormData.forEach((element) => {
      if (data[element.specialServiceName]) {
        if (element.controlTypeCode == 'DDL') {
          displayData[element.specialServiceName] = element.dropDownValues.find(e => e.dropdownID == data[element.specialServiceName]).dropdownValue;
        }
        else {
          displayData[element.specialServiceName] = data[element.specialServiceName];
        }
      }
    });
    this.selectedData.speicalServicePostData = data;
    if(displayData)
    {
      this.selectedData.specialServiceDisplay = displayData;
    }
    
    this.closePopup.emit(this.selectedData);
  }
  cancel() {
    this.closePopup.emit(this.selectedData);
  }
}
