import { Component, OnInit } from '@angular/core';
import { CarrierConfigs, CarrierService, Customerplantconfig, UserCompanyCode, UserRegistration, UserRegistrationDTO } from '../user-registration-model';
import { ActivatedRoute, Router } from '@angular/router';
import { ProcessShipperService } from 'src/app/services/process-shipper.service';
import { DataService } from 'src/app/services/data.service';
import { CompanyCode, Lookup } from 'src/app/functional-components/process-shipping/process-shipping.model';
import { CarrierServiceDTO } from 'src/app/functional-components/models/default-data';

@Component({
    selector: 'app-user-permissions',
    templateUrl: './user-permissions.component.html',
    styleUrls: ['./user-permissions.component.scss']
})
export class UserPermissionsComponent implements OnInit {

    data: any[];
    selectedData: any[] = [];
    userData: UserRegistration = {};
    userDataDto: UserRegistrationDTO = {};
    defaultPlants: Lookup[] = [];
    plantERPs: Lookup[] = [];
    plantCarriers: Lookup[] = [];
    carrierService: Lookup[] = [];
    columns = ["Add", "Plant ID", "Contact Name", "City", "State"]
    plantCompanyColumns = ["Add", "Plant ID", "Company Code"]
    userCompanyCode: CompanyCode[] = [];
    selectedUserCompanyCode: CompanyCode[] = [];
    actualPlantCarrier: CarrierConfigs[];
    plantDefaultERPId: number;
    IntegrationId: number;
    plantCountryCode:string;
    carrierServiceDetails: CarrierServiceDTO[] = [];
    constructor(private activatedRoute: ActivatedRoute, private processShipperService: ProcessShipperService,
        private dataService: DataService, private router: Router) { }

    breadcrumb = 'User Permission';
    id: number;

    ngOnInit(): void {

        this.activatedRoute.params.subscribe(paramsId => {
            this.id = paramsId.id;

        });
        this.processShipperService.getPlantConfigurationData().subscribe(plantData => {
            console.log(plantData);
            this.data = plantData;
            this.processShipperService.getCompanyCode().subscribe(data => {
                this.userCompanyCode = data;
            });
            this.getUsersdata();
        });


    }
    back() {
        this.router.navigate(['/users/']);
    }
    getUsersdata() {
        if (this.id > 0) {
            //this.breadcrumb='Update'
            this.processShipperService.getUsersDatabyId(this.id).subscribe(userData => {
                console.log(userData);
                this.userDataDto = userData;
                this.userData = userData.userRegistration;
                this.userData.toolKitId = userData.userRegistration['toolkitId'];
                this.data.forEach(element => {
                    if (this.userDataDto.customerplantconfigs.filter(x => x.plantid == element.senderId).length > 0) {
                        element.selected = true;
                        this.defaultPlants.push({ textField: element.senderId, valueField: element.senderId })
                    }
                });
                this.userCompanyCode.forEach(element => {
                    if (this.userDataDto.userCompanyCodes.filter(x => parseInt(x.companyCodeId) == element.id).length > 0) {
                        element.selected = true;
                    }
                });
                if (this.userData.plantid) {
                    this.getErp()
                }

            })
        }
    }
    save() {
        this.userDataDto.userRegistration = this.userData;
        this.userDataDto.customerplantconfigs = [];
        if (this.data.filter(x => x.selected == true).length > 0) {
            this.data.filter(x => x.selected == true).forEach(item => {
                const custPlantConfig: Customerplantconfig = { customername: this.userData.userName, plantid: item.senderId }
                this.userDataDto.customerplantconfigs.push(custPlantConfig)
            })
        }
        this.userDataDto.userCompanyCodes = [];
        if (this.userCompanyCode.filter(x => x.selected == true).length > 0) {
            this.userCompanyCode.filter(x => x.selected == true).forEach(item => {
                const companyCode: UserCompanyCode = { userId: this.userData.userName, companyCodeId: item.id.toString(), plantId: item.plantId }
                this.userDataDto.userCompanyCodes.push(companyCode)
            })
        }
        if (this.id > 0) {
            this.processShipperService.updateUser(this.userDataDto).subscribe(plantData => {
                this.dataService.refreshPlant.next(plantData);
                this.back();
            });
        }
    }

    plantSelection(event, item) {
        if (event.checked == true) {
            this.defaultPlants.push({ textField: item.senderId, valueField: item.senderId })
            item.selected = true;
        } else {
            this.defaultPlants.forEach((itemPlant, index) => {
                if (itemPlant.valueField === item.senderId) this.defaultPlants.splice(index, 1);
            });

            item.selected = false;
        }
        console.log('selected', this.selectedData);
    }

    plantCompanySelection(event, item) {

        if (event.checked == true) {
            item.selected = true;
            this.selectedUserCompanyCode.push(item)
        } else {

            this.selectedUserCompanyCode.forEach((itemPlant, index) => {
                if (itemPlant.id === item.id) this.selectedUserCompanyCode.splice(index, 1);
            });
            item.selected = false;
        }
        console.log('selected', this.selectedUserCompanyCode);
    }

    getErp() {

        this.dataService.getERPData(this.userData.plantid).subscribe(data => {
            this.plantERPs = data["erp"].map(x => { return { textField: x.erpName, valueField: x.toolKitId } as Lookup });
            console.log('toolkit', this.userData.toolKitId)
        });
        this.processShipperService.getCarriers(this.userData.plantid).subscribe(data => {
            this.actualPlantCarrier = data;
            //this.plantCarriers = data.map(x => { return { textField: x.carrierCode + ' - ' + x.accountNumber + ' - ' + x.carrierName, valueField: x.id } as Lookup });
            this.plantCarriers = data
                .map(x => {
                    return {
                        textField: (x.useAsCarrierName == true ? (x.carrierCode +
                            (x.accountNumber && x.accountNumber.length > 0 ? ' - ' + x.accountNumber : (x.carrierName && x.carrierName.length > 0 ? ' - ' + x.carrierName : '')))
                            : x.carrierName + (x.accountNumber && x.accountNumber.length > 0 ? '-' + x.accountNumber : '')
                        ),
                        valueField: x.id
                    } as Lookup
                });

            if (this.userData.carrierConfigId != null) {
                this.IntegrationId = this.actualPlantCarrier.find(x => x.id == this.userData.carrierConfigId)?.integrationId;
                this.userData.carrier = this.actualPlantCarrier.find(x => x.id == this.userData.carrierConfigId)?.carrierCode;

                if (this.userData.carrier && this.IntegrationId > 0) {
                    this.getCarrierService(this.userData.carrier, this.IntegrationId);
                } else {
                    this.carrierService = [];
                }
            }
        })

    }


    OnSelecterp() {

        this.userData.defaultErp = this.plantERPs.find(x => x.valueField == this.userData.toolKitId)?.textField;

    }
    getCarrierService(CarrierCode: string, IntegrationId: number) {
        this.processShipperService.getCarrierService(CarrierCode, IntegrationId).subscribe(data => {
            //this.carrierService = data;
            if(this.userData.plantid){
           this.plantCountryCode= this.data.find(x=>x.senderId==this.userData.plantid)?.countryCode;
            }
      this.carrierServiceDetails = data.filter(x => x.countryMappings.indexOf(this.plantCountryCode) >= 0);
            this.carrierService = this.carrierServiceDetails.map(x => { return { textField: x.serviceName, valueField: x.id } as Lookup });
            this.userData.carrierServiceId = this.carrierService.find(x => x.valueField == this.userData.carrierServiceId)?.valueField;
        });
    }
    // getCarrierServices(event) {
    //   const carrierCode = this.actualPlantCarrier.filter(x=>x.carrierName==event.value)[0].carrierCode;
    //     this.processShipperService.getCarrierServices(carrierCode).subscribe(data => {
    //         this.carrierService = data.map(x => { return { textField: x.serviceName, valueField: x.id } as Lookup });
    //     })
    // }
    OnSelectCarrier() {
        if (this.userData.carrierConfigId > 0) {
            this.IntegrationId = this.actualPlantCarrier.find(x => x.id == this.userData.carrierConfigId).integrationId;
            this.userData.carrier = this.actualPlantCarrier.find(x => x.id == this.userData.carrierConfigId).carrierCode;
            this.getCarrierService(this.userData.carrier, this.IntegrationId);


        }
    }
}
