import { Component, OnInit } from "@angular/core";
import { ActivatedRoute, Router } from "@angular/router";
import { DataService } from "src/app/services/data.service";
import { configurationService } from "src/app/services/configuration-service";
import { DropDown } from "src/app/models/special-services.model";
import { packingListConfigurationModel } from "src/app/models/packingListConfiguration.model";
interface UploadEvent {
  originalEvent: Event;
  files: File[];
}
@Component({
  selector: "app-create",
  templateUrl: "./create.component.html",
  styleUrls: ["./create.component.scss"],
})
export class CreatePackagingListComponent implements OnInit {
  dominonDetails = true;
  plants: DropDown[];
  packingListConfigurationModel: packingListConfigurationModel = {
    id: 0,
    plantId: "",
    printFromAddress: false,
    printPackageDetails: false,
    advertisement: false,
    printOrderedQuantity: false,
    csr: false,
    headerText: "",
    footerText: "",
    shippingInstructions: false,
    footerLeftImage: "",
    footerRightImage: "",
    rectangleLogo: false,
    printJobNumber: false,
    triFold: false,
    shipperName: false,
    groupByQuantity: false,
    expenseCodeFormNumber: false,
    printItemNumber: false,
    printBackOrdered: false,
    printJobNumberText: 0,
    orderedByHeaderText: "",
    totalWeight: false,
    printCustId: false,
    breakOutByContainer: false,
    printOrderByAddress: false,
    printStatus: false,
    consolidatedPackingList: false,
    printJobNumberBarcode: false,
  };
  id: any;
  leftFooterImage: any = { name: "" };
  rightFooterImage: any = { name: "" };
  constructor(
    private configurationService: configurationService,
    private dataService: DataService,
    private router: Router,
    private activatedRoute: ActivatedRoute
  ) {}

  ngOnInit(): void {
    this.configurationService.getPlantMasterData().subscribe((data) => {
      this.plants = data;
    });
    this.activatedRoute.params.subscribe((paramsId) => {
      this.id = paramsId.id;
      if (this.id > 0) {
        this.configurationService
          .getPackingListConfigurationbyId(this.id)
          .subscribe((data) => {
            this.packingListConfigurationModel = data;
          });
      }
    });
  }
  back() {
    this.router.navigate(["packingList"]);
  }

  onBasicUploadAutoLeft(event: UploadEvent) {
    let fileReader = new FileReader();
    let value: any;
    for (let file of event.files) {
      fileReader.readAsDataURL(file);
      fileReader.onload = function () {
        // Will print the base64 here.
        value = fileReader.result;
        console.log(fileReader.result);
      };
    }
    this.packingListConfigurationModel.footerLeftImage = value;
    // if(imageType=='Left'){
    //   this.leftFooterImage.nam
    // }
    console.log(event.files[0]);
  }
  onBasicUploadAutoRight(event: UploadEvent) {
    let fileReader = new FileReader();
    for (let file of event.files) {
      fileReader.readAsDataURL(file);
      fileReader.onload = function () {
        // Will print the base64 here.
        console.log(fileReader.result);
      };
    }
    this.rightFooterImage.name = event.files[0].name;
    // if(imageType=='Left'){
    //   this.leftFooterImage.nam
    // }
    console.log(event.files[0]);
  }
  save() {
    if (this.id > 0) {
      this.configurationService
        .updatePackingListConfiguration(this.packingListConfigurationModel)
        .subscribe((data) => {
          console.log(data);
          this.router.navigate(["/packingList/"]);
        });
    } else {
      this.configurationService
        .savePackingListConfiguration(this.packingListConfigurationModel)
        .subscribe((data) => {
          console.log(data);
          this.router.navigate(["/packingList/"]);
        });
    }
  }
}
