import { Component, Input, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { DataService } from 'src/app/services/data.service';
import { ProcessShipperService } from 'src/app/services/process-shipper.service';

@Component({
  selector: 'app-user-registration-list',
  templateUrl: './user-registration-list.component.html',
  styleUrls: ['./user-registration-list.component.scss']
})
export class UserRegistrationListComponent implements OnInit {



  @Input() data =[]

  @Input() columns = ["Action","User Code","User Name","Locked","Full Name","Status","Last Login","Profile"]
  constructor(private processShipperService: ProcessShipperService,
    private dataService: DataService,private router: Router,private route: ActivatedRoute,) { }

  ngOnInit(): void {
    this.processShipperService.getUsers().subscribe(userData=>{
      console.log(userData);
      this.data = userData;
  });
  }

  edit(id,page){
    if(page=='u'){
    this.router.navigate(['users/update/'+id]);
    }else{
      this.router.navigate(['users/userPermission/'+id]);
    }
  }
  create(){
    this.router.navigate(['create'], {relativeTo:this.route});
  }
}
