import {
  Component,
  EventEmitter,
  Input,
  OnInit,
  Output,
  SimpleChanges,
} from "@angular/core";
import { ProcessShipperService } from "src/app/services/process-shipper.service";

@Component({
  selector: "app-delivery-document-popup",
  templateUrl: "./delivery-document-popup.component.html",
  styleUrls: ["./delivery-document-popup.component.scss"],
})
export class DeliveryDocumentPopupComponent implements OnInit {
  data: any;
  date: any;
  @Input() delDocData: any;
  @Output() openDeliveryDocDetail = new EventEmitter<string>();
  columns = [
    "Delivery Number",
    "Job Number",
    "Handling Unit",
    "Weight",
    "Quantity",
    "Tracking No",
    "Ship Cost",
    "Status",
  ];
  itemSectionExpanded = false;

  constructor(private processShipperService: ProcessShipperService) {}

  ngOnInit(): void {
    this.date = new Date();
  }

  search() {
    this.processShipperService
      .getDeliveryDoc(this.dateToISOLikeButLocal(this.date))
      .subscribe((data) => {
        this.data = data;
      });
  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes["delDocData"]?.currentValue) {
      this.date = new Date();
      this.data = changes["delDocData"]?.currentValue;
    }
  }

  openDelDoc(number) {
    this.openDeliveryDocDetail.emit(number);
  }

  dateToISOLikeButLocal(date) {
    const offsetMs = date.getTimezoneOffset() * 60 * 1000;
    const msLocal = date.getTime() - offsetMs;
    const dateLocal = new Date(msLocal);
    const iso = dateLocal.toISOString();
    const isoLocal = iso.slice(0, 19);
    return isoLocal;
  }
}
