import { Component, OnInit } from "@angular/core";
import { Router } from "@angular/router";
import { CarrierConfigurationService } from "src/app/services/carrier-configuration.service";
import { DataService } from "src/app/services/data.service";

@Component({
  selector: "app-carrier-integration-configuration",
  templateUrl: "./carrier-integration-configuration.component.html",
  styleUrls: ["./carrier-integration-configuration.component.scss"],
})
export class CarrierIntegrationConfigurationComponent implements OnInit {
  columns = [
    "Quick Actions",
    "Integration Name",
    "Integration Code",
    "Type",
    "Is Integrated",
    "Ship per Pack",
    "Use Carrier Name",
    "New Special Service",
  ];
  detailSectionExpanded = true;
  showServicesPopup = false;
  showSplServicePopup = false;
  showPackageTypePopup = false;
  data: any = [];
  constructor(
    private ccService: CarrierConfigurationService,
    private dataService: DataService,
    private router: Router
  ) {}

  ngOnInit(): void {
    this.ccService.getCarrierIntegration().subscribe((data) => {
      console.log(data);
      this.data = data;
    });
  }
  edit(id) {}
  delete(id) {}

  addNew() {
    this.router.navigate(["/carrierIntegrationDetail/"]);
  }
}
