import { Component, OnInit } from '@angular/core';
import { UserRegistration, UserRegistrationDTO } from '../user-registration-model';
import { Lookup } from 'src/app/functional-components/process-shipping/process-shipping.model';
import { ActivatedRoute, Router } from '@angular/router';
import { ProcessShipperService } from 'src/app/services/process-shipper.service';
import { DataService } from 'src/app/services/data.service';
import { forkJoin } from 'rxjs';

@Component({
  selector: 'app-create-user-registration',
  templateUrl: './create-user-registration.component.html',
  styleUrls: ['./create-user-registration.component.scss']
})
export class CreateUserRegistrationComponent implements OnInit {

  userDataDto:UserRegistrationDTO={};
  userData: UserRegistration= {};
  states:Lookup[];
  countries:Lookup[];
  userProfile:Lookup[];
  constructor(private activatedRoute: ActivatedRoute,private processShipperService: ProcessShipperService,
    private dataService: DataService,private router: Router) { }

  breadcrumb='New';
  id:number;

  ngOnInit(): void {

    const statePromise = this.processShipperService.getStateData();
    const countryPromise = this.processShipperService.getCountryMasterData();
    const userProfilePromise = this.processShipperService.getUserProfileValues();
    // forkJoin() to make parallel calls
    forkJoin([statePromise, countryPromise,userProfilePromise]).subscribe(responses => {
        let stateData = responses[0];
        let countryData = responses[1];
        let userProfile = responses[2];
        this.countries = countryData
      .map(x => { return { textField: x.country, valueField: x.country } as Lookup });
      this.states = stateData
      .map(x => { return { textField: x.scode, valueField: x.scode } as Lookup });
      this.userProfile = userProfile.map(x=>{return {textField:x,valueField:x}});
    });

    this.activatedRoute.params.subscribe(paramsId => {
      this.id = paramsId.id;

      if(this.id>0){
        this.breadcrumb='Update'
      this.processShipperService.getUsersDatabyId(this.id).subscribe(plantData=>{
        this.userDataDto = plantData;
        console.log(plantData);
        this.userData = plantData.userRegistration;
      })
    }else{
     this.userData.activeStatus ='Active';
    }
    });
  }

  back(){
    this.router.navigate(['/users/']);
  }

  save(){
    this.userDataDto.userRegistration = this.userData;
    if(this.id>0){

this.processShipperService.updateUser(this.userDataDto).subscribe(plantData=>{
  this.router.navigate(['users/userPermission/'+plantData.userRegistration.id]);
  console.log(plantData);
});}else{
this.processShipperService.saveUser(this.userDataDto).subscribe(plantData=>{
  console.log(plantData);
  this.back();
});

    }
  }

}

