import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { GlobalService } from './global.service';
import { ScallingDataDTO } from '../models/scallingData';

@Injectable({
  providedIn: 'root'
})
export class ScallingDataService {

  constructor(private http: HttpClient, private gs: GlobalService) {

  }
  getData(userName: string): Promise<ScallingDataDTO> {
    return this.http.get<ScallingDataDTO>(this.gs.baseURL + 'api/Scalling',{
        params: {
            userName : userName
        }
    }).toPromise();
  }

  
}
