import {
  Component,
  EventEmitter,
  Input,
  OnInit,
  Output,
  SimpleChanges,
} from "@angular/core";
import { DataService } from "src/app/services/data.service";
import {
  CarrierDataModel,
  EntityContainer,
  InterfaceLineItemDetails,
  Lookup,
  LstLineItem,
  LstPackageGroup,
  ObjInternationalDetails,
} from "../process-shipping/process-shipping.model";
import { getScaleWeight } from "../../javascriptfun.js";
import { element } from "protractor";
import { DropDown } from "src/app/models/special-services.model";
import _ from "lodash";
import { InternationalDetailComponent } from "../international-detail/international-detail.component";
import { Console } from "console";
import { ScallingDataService } from "src/app/services/scallingData.service";
import { ScallingDataDTO } from "src/app/models/scallingData";
@Component({
  selector: "app-package-detail",
  templateUrl: "./package-detail.component.html",
  styleUrls: ["./package-detail.component.scss"],
})
export class PackageDetailComponent implements OnInit {
  @Input() containerData: EntityContainer[] = [];
  @Output() containerDataChange = new EventEmitter<any[]>();
  @Input() ItemData: InterfaceLineItemDetails[] = [];
  @Output() ItemDataChange = new EventEmitter<any[]>();
  @Input() boxTypeList: any[];
  @Input() dimentionList: any[];
  @Input() packageType: any[] = [];
  @Input() shipmentSuccessful: boolean = false;
  lstLineItem: LstLineItem[] = [];
  @Output() packageDetails: EventEmitter<EntityContainer[]> = new EventEmitter<
    EntityContainer[]
  >();
  @Output() internationalItems: EventEmitter<LstLineItem[]> = new EventEmitter<
    LstLineItem[]
  >();
  disableItemMoveOption = true;
  scallingData: ScallingDataDTO;
  selectedItemOption: number;
  looseItems: any[] = [];
  itemMoveOptions: Lookup[] = [];
  @Input() packageTypeId: number;
  @Input() autoReviewShip: boolean = false;
  showItemPopup = false;
  selectedBoxType;
  SelectedDimension: string;
  columns = [
    "Item",
    "Item Description",
    "Total Quantity",
    "Balance Quantity",
    "Partial Quantity",
    // "Licence",
  ];

  searchOptions = [
    { name: "Item Description" },
    { name: "Total Quantity" },
    { name: "Balance Quantity" },
    { name: "Partial Quantity" },
  ];
  data: InterfaceLineItemDetails[] = [];
  showAddnewItem = false;
  packageList: EntityContainer[] = [];
  selectedPackage = 0;
  showAddnewBoxType: boolean;
  showFastPack: boolean;
  addNewItemObj: any = {};
  draggeditem: any;
  createNewPackageFlag: boolean;
  addPopupItems: InterfaceLineItemDetails[] = [];
  addItemPopupClone: any;
  showValidationMessage: boolean;

  constructor(public dataService: DataService, public scallingService: ScallingDataService ) {}

  ngOnInit(): void {}

  ngOnChanges(changes: SimpleChanges) {
    // if (changes["boxTypeList"]?.currentValue) {
    //   this.boxTypeList.unshift({
    //     textField: "Add New Box Type",
    //     valueField: 0,
    //   });
    // }

    if (changes["ItemData"]?.currentValue) {
      this.ItemData.forEach((element) => {
        element.balanceQuanity = element.intQuantity;
      });

      this.data = JSON.parse(JSON.stringify(this.ItemData));
      this.data.forEach((element) => {
        element.balanceQuanity = 0;
      });
    }
    // this.lstLineItem = [];
    // this.internationalItems.emit(this.lstLineItem);
    // this.packageList = [];
    // this.packageDetails.emit(this.packageList);
    if (
      changes["containerData"]?.currentValue &&
      changes["containerData"]?.currentValue.length > 0
    ) {
      const tempPackage = changes["containerData"]
        .currentValue as EntityContainer[];
      this.packageList =
        tempPackage && tempPackage.length > 0 ? tempPackage : [];
      this.packageList.forEach((element, index) => {
        element.sno = index + 1;
      });
      this.updateBalItemQuantity();
      if (this.packageList && this.packageList.length > 0) {
        this.packageList.forEach((pack) => {
          pack.lstPackageGroup.forEach((grp) => {
            grp.lstLineItem.forEach((itm) => {
              //fill data for international form
              const isItemExists = this.lstLineItem.find(
                (x) => x.strItemNumber == itm.strItemNumber
              );
              if (isItemExists) {
                //update here
                isItemExists.intOrderedQuantity =
                  parseInt(isItemExists.intOrderedQuantity.toString()) +
                  parseInt(itm.intOrderedQuantity.toString());
              } else {
                this.lstLineItem.push(JSON.parse(JSON.stringify(itm)));
              }
            });
          });
        });
      }

      this.internationalItems.emit(this.lstLineItem);
      this.packageDetails.emit(this.packageList);
      if (!this.ItemData) {
        this.ItemData = [];
      }
    } else {
      this.packageList = [];
    }
    this.itemMoveOptions = [];
    if (this.packageList.length > 0) {
      this.itemMoveOptions.push({
        textField: "Package " + (this.packageList.length + 1).toString(),
        valueField: this.packageList.length + 1,
      });
      this.disableItemMoveOption = false;
      this.selectedItemOption = this.packageList.length + 1;
    } else {
      this.itemMoveOptions.push({ textField: "Package 1", valueField: 1 });
      this.selectedItemOption = 1;
    }
  }

  selectItems(event, i, innerIndex) {
    if (event.checked) {
      this.looseItems[i].items[innerIndex].selected = true;
      this.disableItemMoveOption = false;
    } else {
      this.looseItems[i].items[innerIndex].selected = false;
    }
  }

  moveAll() {
    this.ItemData.forEach((element) => {
      element.partialQuanity = element.balanceQuanity;
    });

    this.moveItem();
  }

  moveItem() {
    const itemsToMove = this.ItemData.filter(
      (x) =>
        x.partialQuanity &&
        x.partialQuanity > 0 &&
        x.partialQuanity <= x.balanceQuanity
    );
    this.moveItemtoPackage(itemsToMove);
  }
  moveItemtoPackage(itemsToMove: any) {
    if (itemsToMove.length > 0) {
      const isPackExists = this.packageList.find(
        (x) => x.sno == this.selectedItemOption
      );
      if (isPackExists) {
        const pack = this.packageList.find(
          (x) => x.sno == this.selectedPackage
        );
        itemsToMove.forEach((element) => {
          //const element = itemsToMove[0];
          const tempItems = pack.lstPackageGroup[0].lstLineItem.find(
            (x) => x.strItemNumber == element.strItemNumber
          );
          if (tempItems) {
            tempItems.intOrderedQuantity =
              parseInt(tempItems.intOrderedQuantity.toString()) +
              parseInt(element.partialQuanity);
          } else {
            // add
            let packageItem = {} as LstLineItem;
            packageItem.strItemNumber = element.strItemNumber;
            packageItem.intTotalItemQuantity = element.intQuantity;
            packageItem.strItemDescription = element.strDescription;
            packageItem.intOrderedQuantity = element.partialQuanity
              ? element.partialQuanity
              : element.balanceQuanity;

            let internationaldetail = {} as ObjInternationalDetails;
            packageItem.objInternationalDetails = internationaldetail;
            pack.lstPackageGroup[0].lstLineItem.push(packageItem);
          }
        });
      } else {
        let newPackage: EntityContainer = {} as EntityContainer;
        newPackage.sno = this.selectedItemOption;
        newPackage.isprepacked = false;
        newPackage.strTrackingNumber = "";
        newPackage.intHandlingUnitID = 0;
        newPackage.dblTotalContainerCost = 100;
        newPackage.dblTotalWeight = 10; //this.packageList[0].dblTotalWeight;
        newPackage.lstPackageGroup = [];
        let packageGroup = {} as LstPackageGroup;
        packageGroup.lstLineItem = [];
        itemsToMove.forEach((element) => {
          let packageItem = {} as LstLineItem;
          packageItem.strItemNumber = element.strItemNumber;
          packageItem.intTotalItemQuantity = element.intQuantity;
          packageItem.strItemDescription = element.strDescription;
          packageItem.intOrderedQuantity = element.partialQuanity
            ? element.partialQuanity
            : element.balanceQuanity;
          packageItem.objInternationalDetails = {} as ObjInternationalDetails;

          packageGroup.lstLineItem.push(packageItem);
        });
        newPackage.lstPackageGroup.push(packageGroup);
        this.packageList.push(newPackage);
        //this.packageList.sort((a, b) => b.sno - a.sno);
        this.itemMoveOptions.push({
          textField: "Package " + (newPackage.sno + 1).toString(),
          valueField: newPackage.sno + 1,
        });
        this.selectedItemOption =
          this.itemMoveOptions[this.itemMoveOptions.length - 1].valueField;
        this.disableItemMoveOption = false;
      }

      this.updateBalItemQuantity();
      this.ItemData.forEach((element) => {
        element.partialQuanity = 0;
      });
    }
  }
  newSno() {
    if (this.packageList.length == 0) return 1;
    let max = this.packageList[0].sno;
    for (let i = 1; i < this.packageList.length; ++i) {
      if (this.packageList[i].sno > max) {
        max = this.packageList[i].sno;
      }
    }
    return max + 1;
  }
  updateBalItemQuantity() {
    if (this.ItemData && this.ItemData.length > 0) {
      this.ItemData.forEach((item) => {
        item.balanceQuanity = 0;
        item.orderedQuantity = 0;

        this.packageList.forEach((pack) => {
          if (pack.lstPackageGroup && pack.lstPackageGroup.length > 0) {
            pack.lstPackageGroup.forEach((grp) => {
              if (grp.lstLineItem && grp.lstLineItem.length > 0) {
                const packItemList = grp.lstLineItem
                  .filter((x) => x.strItemNumber == item.strItemNumber)
                  .map((x) => x.intOrderedQuantity)
                  .reduce((sum, current) => sum + current, 0);
                item.orderedQuantity =
                  Number(item.orderedQuantity) + Number(packItemList);
              }
            });
          }
        });

        item.balanceQuanity = item.intQuantity - item.orderedQuantity;
      });
    }

    this.lstLineItem = [];
    if (this.packageList && this.packageList.length > 0) {
      this.packageList.forEach((pack, index) => {
        pack.sno = index + 1;
        pack.lstPackageGroup.forEach((grp) => {
          grp.lstLineItem.forEach((itm) => {
            if (this.ItemData && this.ItemData.length > 0) {
              const tempItem = this.ItemData.find(
                (x) => x.strItemNumber == itm.strItemNumber
              );
              if (tempItem) {
                itm.balanceQuantity = tempItem.balanceQuanity;
              }
            }

            //fill data for international form
            const isItemExists = this.lstLineItem.find(
              (x) => x.strItemNumber == itm.strItemNumber
            );
            if (isItemExists) {
              //update here
              isItemExists.intOrderedQuantity =
                Number(isItemExists.intOrderedQuantity) +
                Number(itm.intOrderedQuantity.toString());
            } else {
              this.lstLineItem.push(JSON.parse(JSON.stringify(itm)));
            }
          });
        });
      });
    }
    //this.packageList.sort((a, b) => b.sno - a.sno);
    this.internationalItems.emit(this.lstLineItem);
    this.packageDetails.emit(this.packageList);
  }

  addItemPopup(pack: EntityContainer) {
    this.data = JSON.parse(JSON.stringify(this.ItemData));
    this.selectedPackage = pack.sno;
    this.showItemPopup = true;
    this.data.forEach((element) => {
      if (!pack.lstPackageGroup || pack.lstPackageGroup.length == 0)
        pack.lstPackageGroup = [{ lstLineItem: [] } as LstPackageGroup];

      const tempItem = pack.lstPackageGroup[0].lstLineItem.find(
        (x) => x.strItemNumber == element.strItemNumber
      );
      if (tempItem) {
        element.partialQuanity = tempItem.intOrderedQuantity;
      }
    });

    console.log(pack);
  }

  addItemToPackage() {
    const pack = this.packageList.find((x) => x.sno == this.selectedPackage);
    if (pack) {
      this.data.forEach((element) => {
        if (element.partialQuanity > 0) {
          if (!pack.lstPackageGroup || pack.lstPackageGroup.length == 0)
            pack.lstPackageGroup = [{ lstLineItem: [] } as LstPackageGroup];
          const tempItems = pack.lstPackageGroup[0].lstLineItem.find(
            (x) => x.strItemNumber == element.strItemNumber
          );
          if (tempItems) {
            tempItems.intOrderedQuantity = element.partialQuanity;
          } else {
            // add
            let packageItem = {} as LstLineItem;
            packageItem.strItemNumber = element.strItemNumber;
            packageItem.intTotalItemQuantity = element.intQuantity;
            packageItem.strItemDescription = element.strDescription;
            packageItem.intOrderedQuantity = element.partialQuanity;

            let internationaldetail = {} as ObjInternationalDetails;
            packageItem.objInternationalDetails = internationaldetail;
            pack.lstPackageGroup[0].lstLineItem.push(packageItem);
          }
        }
      });
    }
    this.showItemPopup = false;
    this.selectedPackage = 0;
    this.updateBalItemQuantity();
  }
  packAllItemToPackage() {
    const pack = this.packageList.find((x) => x.sno == this.selectedPackage);
    if (pack) {
      this.data.forEach((element) => {
        {
          if (!pack.lstPackageGroup || pack.lstPackageGroup.length == 0)
            pack.lstPackageGroup = [{ lstLineItem: [] } as LstPackageGroup];
          const tempItems = pack.lstPackageGroup[0].lstLineItem.find(
            (x) => x.strItemNumber == element.strItemNumber
          );
          if (tempItems) {
            tempItems.intOrderedQuantity =
              element.intQuantity - (element.partialQuanity ?? 0);
          } else {
            // add
            let packageItem = {} as LstLineItem;
            packageItem.strItemNumber = element.strItemNumber;
            packageItem.intTotalItemQuantity = element.intQuantity;
            packageItem.strItemDescription = element.strDescription;
            packageItem.intOrderedQuantity =
              element.intQuantity - (element.partialQuanity ?? 0);

            let internationaldetail = {} as ObjInternationalDetails;
            packageItem.objInternationalDetails = internationaldetail;
            pack.lstPackageGroup[0].lstLineItem.push(packageItem);
          }
        }
      });
    }
    this.showItemPopup = false;
    this.selectedPackage = 0;
    this.updateBalItemQuantity();
  }
  cancelItemToPackage() {
    this.showItemPopup = false;
    if (this.createNewPackageFlag) {
      this.packageList.splice(-1);
    }
    this.createNewPackageFlag = false;
    this.selectedPackage = 0;
  }
  deletePackage(listItem) {
    const packIndex = this.packageList.findIndex((x) => x.sno == listItem.sno);
    this.packageList.splice(packIndex, 1);

    const packIndex2 = this.itemMoveOptions.findIndex(
      (x) => x.valueField == listItem.sno
    );
    this.itemMoveOptions.splice(packIndex2, 1);
    this.updateBalItemQuantity();
    //this.dataService.packagelist.next(this.packageList);
  }
  deleteItemFromPackage(pack, item) {
    const itemIndex = pack?.lstPackageGroup[0]?.lstLineItem.findIndex(
      (x) => x.strItemNumber == item.strItemNumber
    );
    pack?.lstPackageGroup[0]?.lstLineItem.splice(itemIndex, 1);

    if (pack?.lstPackageGroup[0]?.lstLineItem.length == 0) {
      const packIndex = this.packageList.findIndex((x) => x.sno == pack.sno);
      this.packageList.splice(packIndex, 1);

      const packIndex2 = this.itemMoveOptions.findIndex(
        (x) => x.valueField == pack.sno
      );
      this.itemMoveOptions.splice(packIndex2, 1);
    }
    this.updateBalItemQuantity();
    this.dataService.packagelist.next(this.packageList);
  }
  boxTypeChange(event, index) {
    this.selectedBoxType = this.boxTypeList.find(
      (x) => x.textField == event.value
    ).valueField;
    this.packageList[index].strBoxType = event.value; //this.boxTypeList.find(x => x.valueField == this.selectedBoxType).textField;
    // if (this.selectedBoxType == 0) {
    //   this.showAddnewBoxType = true;
    // }
  }

  dimentionsChange(event, index) {
    var dimensionValue = event.value;
    let Arrdimension = dimensionValue.split("X");
    if (Arrdimension) {
      this.packageList[index].dblWidth = Arrdimension[0];
      this.packageList[index].dblHeight = Arrdimension[1];
      this.packageList[index].dblLength = Arrdimension[2];
    }
  }

  addNewItem() {
    this.addPopupItems.forEach((element) => {
      this.ItemData.push(element);
    });
    this.addNewItemObj = {};
    this.showAddnewItem = false;
  }
  createPackage() {
    this.createNewPackageFlag = true;
    const itemToPush = {
      sno: this.packageList.length + 1,
      lstPackageGroup: [],
      isprepacked: false,
    } as EntityContainer;
    this.packageList.push(itemToPush);
    this.selectedItemOption = this.packageList.length + 1;
    this.addItemPopup(itemToPush);
  }
  keyFunc() {
    this.updateBalItemQuantity();
  }

  addItem(newItem: any) {
    if (this.packageList) {
      for (let index = 0; index < newItem.length; index++) {
        this.packageList.push(newItem[index]);
      }
      for (
        let newIndex = 0;
        newIndex < newItem[0].lstPackageGroup[0].lstLineItem.length;
        newIndex++
      ) {
        const newItemDataElement =
          newItem[0].lstPackageGroup[0].lstLineItem[newIndex];

        for (let i = 0; i < this.ItemData.length; i++) {
          const itemDataElement = this.ItemData[i];

          if (
            newItemDataElement.strItemNumber == itemDataElement.strItemNumber
          ) {
            itemDataElement.balanceQuanity = newItemDataElement.balanceQuantity;
          }
        }
      }
    } else {
      this.packageList = newItem;
    }
    this.showFastPack = false;
  }
  dragStart(product: any) {
    let item = JSON.parse(JSON.stringify(product));
    console.log("P", item);
    item.partialQuanity = item.partialQuanity
      ? item.partialQuanity
      : item.balanceQuanity;
    this.draggeditem = item;
  }

  drop(dropBox) {
    console.log("dropbox", dropBox);
    let draggedItems = this.ItemData.filter((x) => x.selected);
    this.selectedItemOption = dropBox.sno;
    this.selectedPackage = dropBox.sno;
    if (draggedItems && draggedItems.length > 0) {
      this.moveItemtoPackage(draggedItems);
    } else {
      this.moveItemtoPackage([this.draggeditem]);
    }
  }

  dragEnd() {
    this.ItemData.forEach((element) => {
      element.selected = false;
    });

    this.draggeditem = null;
  }

  deletePopupItem(index) {
    this.addPopupItems.splice(index, 1);
  }

  addIteminPopup() {
    if (
      this.addNewItemObj &&
      this.addNewItemObj.item &&
      this.addNewItemObj.toatlQuantity &&
      this.addNewItemObj.description
    ) {
      this.showValidationMessage = false;
      let itemData: InterfaceLineItemDetails = {
        strItemNumber: this.addNewItemObj.item,
        intQuantity: this.addNewItemObj.toatlQuantity,
        strDescription: this.addNewItemObj.description,
        intHandlingUnitID: 0,
        strLineKey: "",
        strOrderedUnitOfMeasure: "",
        strLineItemNMFC: "",
        strLineItemSub: "",
        strLineItemClass: "",
        strHarmonizedCode: "",
        strJobNumber: "",
        strISBN: "",
        strFSC: "",
        strPoNumber: "",
        intWeight: 0,
        objInternationalDetails: undefined,
        partialQuanity: 0,
        balanceQuanity: 0,
        orderedQuantity: 0,
      };
      this.addPopupItems.push(itemData);
      this.addItemPopupClone = JSON.parse(JSON.stringify(this.addPopupItems));
      this.addNewItemObj = {};
    } else {
      this.showValidationMessage = true;
    }
  }
  getWeight(index : number){
    this.scallingService
    .getData("DESKTOP-T7VAHKM")
    .then((data) => {
      this.scallingData = data;
        var Value =  getScaleWeight('','','','','',this.scallingData.VendorId,this.scallingData.ProductId,'USB');
        this.packageList[index].dblTotalWeight = Value;
      })
    .catch((error) => {
      console.log(error);
    });
    
  }
  searchNewlyAddedItems(event) {
    console.log(this.addNewItemObj.search);
    // this.addPopupItems = JSON.parse(JSON.stringify(this.addItemPopupClone));
    // this.addPopupItems = [];
    // if (this.addNewItemObj.search) {
    //   this.addPopupItems = this.addPopupItems.filter((e) =>
    //     e.strDescription.includes(this.addNewItemObj.search)
    //   );
    // }
  }
}
