import { HttpClient, HttpHeaders } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { GlobalService } from "./global.service";
import { boxTypeConfigurationModel } from "../models/boxTypeConfiguration.model";
import { DropDown } from "../models/special-services.model";

@Injectable({
  providedIn: "root",
})
export class BoxTypeConfigurationService {
  constructor(private http: HttpClient, private gs: GlobalService) {}

  get() {
    return this.http.get<boxTypeConfigurationModel[]>(
      this.gs.baseURL + "api/BoxTypesConfiguration"
    );
  }

  getBoxType(id) {
    return this.http.get<boxTypeConfigurationModel>(
      this.gs.baseURL + "api/BoxTypesConfiguration/" + id
    );
  }

  getPlantMasterData() {
    return this.http.get<DropDown[]>(
      this.gs.baseURL + "api/MasterData/GetPlants"
    );
  }

  save(data: boxTypeConfigurationModel) {
    const headers = new HttpHeaders({ "Content-Type": "application/json" });
    return this.http.post<any>(
      this.gs.baseURL + "api/BoxTypesConfiguration",
      data,
      {
        headers: headers,
      }
    );
  }

  update(data: boxTypeConfigurationModel) {
    const headers = new HttpHeaders({ "Content-Type": "application/json" });
    return this.http.put<any>(
      this.gs.baseURL + "api/BoxTypesConfiguration/{id}" + data.id,
      data,
      { headers: headers }
    );
  }
}
