import { HttpClient, HttpHeaders } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { GlobalService } from "./global.service";
import {
  ToolKitDTO,
  ToolKit,
  DefinitionValue,
} from "../functional-components/configuration/toolKit-configration/tool-kit-configration.model";
import { DropDown } from "../models/special-services.model";
import {
  CompanyCode,
  Lookup,
} from "../functional-components/process-shipping/process-shipping.model";

@Injectable({
  providedIn: "root",
})
export class ToolkitServiceService {
  constructor(private http: HttpClient, private gs: GlobalService) {}

  getToolkitdata(selectedPlantId: string) {
    return this.http.get<ToolKit[]>(
      this.gs.baseURL + "api/ToolKit?" + "PlantId=" + selectedPlantId
    );
  }

  getToolkitDataById(Id: number) {
    return this.http.get<ToolKitDTO>(this.gs.baseURL + "GetERP/" + Id);
  }

  delete(Id: number) {
    return this.http.delete(this.gs.baseURL + "api/ToolKit/" + Id);
  }

  saveerpdata(data: ToolKitDTO) {
    const headers = new HttpHeaders({ "Content-Type": "application/json" });
    return this.http.post<any>(this.gs.baseURL + "api/ToolKit", data, {
      headers: headers,
    });
  }

  updateerpdata(data: ToolKitDTO) {
    const headers = new HttpHeaders({ "Content-Type": "application/json" });
    return this.http.put<any>(
      this.gs.baseURL + "api/ToolKit/" + data.toolKit.id,
      data,
      { headers: headers }
    );
  }

  getPlantMasterData() {
    return this.http.get<any[]>(this.gs.baseURL + "api/MasterData/GetPlants");
  }
  getLookuptype() {
    return this.http.get<DefinitionValue[]>(
      this.gs.baseURL + "api/MasterData/GetLookupType"
    );
  }

  getCompanyCode() {
    return this.http.get<CompanyCode[]>(
      this.gs.baseURL + "api/MasterData/GetCompanyCode"
    );
  }

  getEflowClient() {
    return this.http.get<any[]>(
      this.gs.baseURL + "api/MasterData/GetEflowClient"
    );
  }
  getShipmentStatus() {
    return this.http.get<DefinitionValue[]>(
      this.gs.baseURL + "api/MasterData/GetShipmentStatus"
    );
  }

  getBODVersion() {
    return this.http.get<DefinitionValue[]>(
      this.gs.baseURL + "api/MasterData/GetBodVersion"
    );
  }
}
