import { Component, OnInit } from "@angular/core";
import { Router } from "@angular/router";
import { configurationService } from "src/app/services/configuration-service";
import { DataService } from "src/app/services/data.service";

@Component({
  selector: "app-ltl-configuration",
  templateUrl: "./ltl-configuration.component.html",
  styleUrls: ["./ltl-configuration.component.scss"],
})
export class LtlConfigurationComponent implements OnInit {
  administrationList = false;
  batchProcessList = false;
  configurationList = false;
  ERPSetupList = false;
  hazardousList = false;
  carriersIntegrationList = false;
  carriersNonIntegrationList = true;
  toolKit = false;
  dominonDetails = true;
  trackingNumber = true;

  columns = [
    "Quick Actions",
    "Range Type",
    "From Postal Code",
    "To Postal Code",
    "Unit Definition",
    "Unit Value",
    "Price Per Unit",
  ];
  data: any;

  fastVoid = false;

  showAddAccessorialPopup = false;
  plants: any;
  ratingMethod: any;
  brokerName: any[] = [{ value: "EFI" }, { value: "EFICA" }];
  packingMode: any[] = [{ value: "Parcel" }, { value: "Palet" }];
  ratingType: any[] = [
    { value: "Regular Zip Code" },
    { value: "3 Digit Zip Code" },
    { value: "City\\State" },
  ];
  rateSource: any[] = [{ value: "File" }, { value: "Web Service" }];
  deliveryAreaSurcharge: any[] = [
    { value: "Per Package" },
    { value: "Per Shipment" },
  ];
  constructor(
    private configurationService: configurationService,
    private dataService: DataService,
    private router: Router
  ) {}

  ngOnInit(): void {
    this.configurationService.getPlantMasterData().subscribe((data) => {
      this.plants = data;
    });
    this.configurationService.getDefinationValue("RTMTD").subscribe((data) => {
      this.ratingMethod = data;
    });
  }
  edit(id) {
    this.router.navigate(["create-ship-via-code-configuration/" + id]);
  }
}
