import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-fast-void',
  templateUrl: './fast-void.component.html',
  styleUrls: ['./fast-void.component.scss']
})
export class FastVoidComponent implements OnInit {
  columns = [
    "",
    "Ship Date",
    "Delivery Doc",
    "Carrior Name",
    "Service Type",
    "Weight",
    "Cost",
    "Tracking no",
    "Status",
  ];
  router: any;
  svcService: any;
  data: any;

  fastVoid = false;
  constructor() { }

  ngOnInit(): void {
    this.svcService.get().subscribe((data) => {
      this.data = data;
    });
  }
  edit(id) {
    this.router.navigate(["create-ship-via-code-configuration/" + id]);
  }

}
