import { Component, OnInit, ViewChild } from "@angular/core";
import {
  InterfaceType,
  ToolKit,
  Planterpconfig,
  DefinitionValue,
  ToolKitDTO,
  CompanyCode,
} from "../tool-kit-configration.model";
import { DataService } from "src/app/services/data.service";
import { ToolkitServiceService } from "src/app/services/toolkit-service.service";
import { ActivatedRoute, Router } from "@angular/router";
import { Lookup } from "src/app/functional-components/process-shipping/process-shipping.model";
import { DropDown } from "src/app/models/special-services.model";
import { forkJoin, of } from "rxjs";
import { ProcessShipperService } from "src/app/services/process-shipper.service";

@Component({
  selector: "app-create-erp-configration",
  templateUrl: "./create-erp-configration.component.html",
  styleUrls: ["./create-erp-configration.component.scss"],
  styles: [
    `
      :host ::ng-deep .p-cell-editing {
        padding-top: 0 !important;
        padding-bottom: 0 !important;
      }
    `,
  ],
})
export class CreateErpConfigrationComponent implements OnInit {
  erp: ToolKit = {} as ToolKit;
  interfaceType: InterfaceType = { packageSelector: false } as InterfaceType;
  planterpconfig: Planterpconfig = { active: true } as Planterpconfig;
  isEdit: boolean = false;
  id: number;
  breadcrumb = "New";
  shipmentStatusList: DefinitionValue[] = [];
  LookupTypeList: DefinitionValue[] = [];
  plantList: Lookup[] = [];
  BodVersionList: DefinitionValue[] = [];
  pullBySectionExpanded = true;
  defaultPackaging: any[] = [
    { value: "Smart Packaging" },
    { value: "XYZ Packaging" },
  ];

  autopackingOptions: any[] = [];
  shipStatus: any[] = [{ value: "All" }, { value: "Plant" }, { value: "ship" }];
  customConfigurationOptions: any[] = [
    { name: "Custom Boolean 1", code: "NY" },
    { name: "Custom Boolean 2", code: "RM" },
  ];
  selectedCustomConfiguration: any;
  companyCodes: CompanyCode[] = [];
  columns = ["Quick Actions", "Code", "Description", "Active"];
  showAddNewCompany = false;
  addNewItemObj: any = {};
  ddlEflowClient: any;
  constructor(
    private activatedRoute: ActivatedRoute,
    private toolkitservices: ToolkitServiceService,
    private dataService: DataService,
    private processShipperService: ProcessShipperService,
    private router: Router
  ) {
    this.getPlantData();
  }

  ngOnInit(): void {
    this.activatedRoute.params.subscribe((paramsId) => {
      this.id = paramsId.id;
      if (this.id > 0) {
        this.isEdit = true;
        this.breadcrumb = "Update";
        this.getErpById();
      }
    });
    this.processShipperService
      .getDefinationValue("AUTOPACK")
      .subscribe((data) => {
        this.autopackingOptions = data;
        console.log("freigt", this.autopackingOptions);
      });
    this.getShipmentStatus();
    this.getLookupType();
    this.getPlantData();
    this.getBodVersiondata();
    this.getEflowClientdata();
  }

  ngAfterViewChecked() {}

  getBodVersiondata() {
    this.toolkitservices.getBODVersion().subscribe((responses) => {
      this.BodVersionList = responses.map((x) => {
        return { description: x.description, id: x.id } as DefinitionValue;
      });
    });
  }
  getEflowClientdata() {
    this.toolkitservices.getEflowClient().subscribe((responses) => {
      this.ddlEflowClient = responses.map((x) => {
        return {
          description: x.dropdownValue,
          id: x.definationId,
        } as DefinitionValue;
      });
    });
  }
  getLookupType() {
    this.toolkitservices.getLookuptype().subscribe((responses) => {
      this.LookupTypeList = responses.map((x) => {
        return { description: x.description, id: x.id } as DefinitionValue;
      });
    });
  }

  getShipmentStatus() {
    this.toolkitservices.getShipmentStatus().subscribe((responses) => {
      this.shipmentStatusList = responses.map((x) => {
        return { description: x.description, id: x.id } as DefinitionValue;
      });
    });
  }
  getPlantData() {
    this.toolkitservices.getPlantMasterData().subscribe((responses) => {
      this.plantList = responses.map((x) => {
        return {
          textField: x.plantId?.toUpperCase(),
          valueField: x.plantId?.toUpperCase(),
        } as Lookup;
      });

      console.log(responses);
    });
  }
  getErpById() {
    this.toolkitservices.getToolkitDataById(this.id).subscribe((responses) => {
      this.erp = responses.toolKit;
      this.planterpconfig = responses.planterpconfig;
      this.interfaceType = responses.interfaceType ?? ({} as InterfaceType);
      this.companyCodes = responses.companyCodes;
      this.planterpconfig.blnactive =
        this.planterpconfig.active == "true" ? true : false;
      this.planterpconfig.blndefaulterp =
        this.planterpconfig.defaulterp == "true" ? true : false;

      console.log(responses);
    });
  }
  backToToolkit() {
    this.router.navigate(["/toolkit-configration/"]);
  }

  plantChange() {
    // this.toolkitservices.getCompanyCode().subscribe((data) => {
    //   if (data) {
    //     this.companyCodes = data.filter((x) => x.plantId == this.erp.plantId);
    //   }
    // });
  }

  saveErp() {
    console.log(this.erp.pullByJobDefault);
    this.erp.shipmentType = this.erp.shipmentType ?? false;
    this.erp.shipmentTypeText = this.erp.shipmentTypeText ?? "";
    this.erp.interfaceReference2Description =
      this.erp.interfaceReference2Description ?? "";
    this.erp.eiurl = this.erp.eiurl ?? "";
    this.erp.completeCheckBoxText = this.erp.completeCheckBoxText ?? "";
    this.interfaceType.interfaceDescription =
      this.interfaceType.interfaceDescription ?? "";
    this.erp.interfacePath = this.erp.interfacePath ?? "";
    this.planterpconfig.defaulterp =
      this.planterpconfig.blndefaulterp == true ? "true" : "false";
    this.planterpconfig.active =
      this.planterpconfig.blnactive == true ? "true" : "false";
    let postData: ToolKitDTO = {
      toolKit: this.erp,
      interfaceType: this.interfaceType,
      planterpconfig: this.planterpconfig,
      companyCodes: this.companyCodes,
    };
    if (this.id > 0) {
      this.toolkitservices.updateerpdata(postData).subscribe((erpdata) => {
        console.log(erpdata);
        this.backToToolkit();
      });
    } else {
      this.toolkitservices.saveerpdata(postData).subscribe((erpdata) => {
        console.log(erpdata);
        this.backToToolkit();
      });
    }
  }

  edit(id) {
    this.addNewItemObj = this.companyCodes.filter((x) => x.id == id)[0];
    this.showAddNewCompany = true;
  }
  delete(id) {}
  cancel() {
    this.showAddNewCompany = false;
  }
  addCompanyCode() {
    this.companyCodes.push(this.addNewItemObj);

    this.companyCodes.forEach((element) => {
      element.plantId = this.erp.plantId;
      element.toolKitId = element.toolKitId ?? "0";
    });

    this.showAddNewCompany = false;
  }
}
