import { HttpClient, HttpHeaders } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { GlobalService } from "./global.service";
import {
  CarrierDataModel,
  CarrierService,
  CompanyCode,
  Dimension,
  PlantDataModel,
  SelectedData,
  SenderConfiguration,
  ShipmentDataModel,
  UserDefaultDataModel,
  XcarrierCode,
} from "../functional-components/process-shipping/process-shipping.model";
import {
  CountryMasterData,
  CurrencyMasterData,
  PlantConfiguration,
  StateMasterData,
} from "../functional-components/configuration/plant-configuration/plant-configuration-model";
import { DropDown } from "../models/special-services.model";
import {
  BoxTypeDTO,
  CarrierServiceDTO,
  CarriersDTO,
  PackageTypeDTO,
} from "../functional-components/models/default-data";
import {
  CarrierConfigs,
  PlantERPConfig,
  UserCompanyCode,
  UserRegistration,
  UserRegistrationDTO,
} from "../functional-components/configuration/user-registration/user-registration-model";
import { DropDownValue, InternationalShipDetailForm } from "../functional-components/process-shipping/international-form/international-form.model";
import { eFlowClient } from "../models/eflowClient.model";

@Injectable({
  providedIn: "root",
})
export class ProcessShipperService {
  constructor(private http: HttpClient, private gs: GlobalService) {}

  getDefaultData() {
    return this.http.get<UserDefaultDataModel>(
      this.gs.baseURL + "api/UserDefaultData"
    );
  }

  getPlantDetail(senderId: string) {
    return this.http.get<SenderConfiguration>(
      this.gs.baseURL + "GetPlantDetail/" + senderId
    );
  }
  getCarriers(senderId: string) {
    return this.http.get<CarriersDTO[]>(
      this.gs.baseURL + "GetAssociatedCarriers/" + senderId
    );
  }
  getIntegrationId() {
    return this.http.get<number[]>(this.gs.baseURL + "GetIntegrationId/");
  }
  getBoxTypeData(senderId: string) {
    return this.http.get<BoxTypeDTO[]>(
      this.gs.baseURL + "GetBoxTypeData/" + senderId
    );
  }
  getDimensions() {
    return this.http.get<Dimension[]>(this.gs.baseURL + "GetDimensions");
  }
  getShipViaCode(senderId: string) {
    return this.http.get<XcarrierCode[]>(
      this.gs.baseURL + "GetShipViaCode/" + senderId
    );
  }
  getCustomizeDelDoc(plantId: string) {
    return this.http.get<any>(
      this.gs.baseURL + "getCustomizeDelDoc/" + plantId
    );
  }
  getCarrierService(CarrierCode: string, IntegrationId: number) {
    return this.http.get<CarrierServiceDTO[]>(
      this.gs.baseURL + "GetCarrierService/" + CarrierCode + "/" + IntegrationId
    );
  }
  getPackageType(carrierCode: string) {
    return this.http.get<PackageTypeDTO[]>(
      this.gs.baseURL + "GetPackageType/" + carrierCode
    );
  }
  getPaymentTypes(carrierCode: string) {
    return this.http.get<string[]>(
      this.gs.baseURL + "GetPaymentTypes/" + carrierCode
    );
  }

  //******************************** */
  getPlantConfigurationData() {
    return this.http.get<PlantConfiguration[]>(
      this.gs.baseURL + "api/PlantConfiguration"
    );
  }

  getPlantConfigurationDatabyId(id: number) {
    return this.http.get<PlantConfiguration>(
      this.gs.baseURL + "api/PlantConfiguration/" + id
    );
  }

  getEflowConfiguration() {
    return this.http.get<any[]>(this.gs.baseURL + "api/EFlow");
  }

  getCarrierData(data: SelectedData) {
    return this.http.get<CarrierDataModel>(
      this.gs.baseURL +
        "api/CarrierData?" +
        "PlantId=" +
        data.PlantId +
        "&IntegrationId=" +
        data.IntegrationId +
        "&IntegrationCode=" +
        data.IntegrationCode +
        "&CarrierCode=" +
        data.CarrierCode +
        "&AccountId=" +
        data.AccountNumber +
        "&CountryCode=" +
        data.CountryCode
    );
  }

  pullPlannedShipData(data: any) {
    return this.http.get<ShipmentDataModel>(
      this.gs.baseURL +
        "api/PullPlannedShipData?" +
        "PlantId=" +
        data.PlantId +
        "&ERPName=DSF" + //+ data.ERPName +
        "&PullType=" +
        data.PullType +
        "&PullBy=" +
        data.PullBy +
        "&Identifier1=" +
        data.Identifier1 +
        "&Identifier2=" +
        data.Identifier2
    );
  }

  shipData(data: ShipmentDataModel) {
    const headers = new HttpHeaders({ "Content-Type": "application/json" });
    return this.http.post<any>(this.gs.baseURL + "api/PostShipment", data, {
      headers: headers,
    });
  }

  getPlantdata(selectedPlant: string) {
    return this.http.get<PlantDataModel>(
      this.gs.baseURL + "api/PlantData?" + "PlantId=" + selectedPlant
    );
  }

  getStateData() {
    return this.http.get<StateMasterData[]>(
      this.gs.baseURL + "api/MasterData/GetStateDetails"
    );
  }

  getCurrencyData() {
    return this.http.get<CurrencyMasterData[]>(
      this.gs.baseURL + "api/MasterData/GetCurrencyCodes"
    );
  }

  getCountryMasterData() {
    return this.http.get<CountryMasterData[]>(
      this.gs.baseURL + "api/MasterData/GetCountryDetails"
    );
  }

  updatePlantConfiguration(data: PlantConfiguration) {
    const headers = new HttpHeaders({ "Content-Type": "application/json" });
    return this.http.put<any>(
      this.gs.baseURL + "api/PlantConfiguration/" + data.id,
      data,
      { headers: headers }
    );
  }

  savePlantConfiguration(data: PlantConfiguration) {
    const headers = new HttpHeaders({ "Content-Type": "application/json" });
    return this.http.post<any>(
      this.gs.baseURL + "api/PlantConfiguration",
      data,
      { headers: headers }
    );
  }

  getUsers() {
    return this.http.get<UserRegistration[]>(
      this.gs.baseURL + "api/UserRegistrations"
    );
  }

  getUsersDatabyId(id: number) {
    return this.http.get<UserRegistrationDTO>(
      this.gs.baseURL + "api/UserRegistrations/" + id
    );
  }

  updateUser(data: UserRegistrationDTO) {
    const headers = new HttpHeaders({ "Content-Type": "application/json" });
    return this.http.put<any>(
      this.gs.baseURL + "api/UserRegistrations/" + data.userRegistration.id,
      data,
      { headers: headers }
    );
  }

  saveUser(data: UserRegistrationDTO) {
    const headers = new HttpHeaders({ "Content-Type": "application/json" });
    return this.http.post<any>(
      this.gs.baseURL + "api/UserRegistrations",
      data,
      { headers: headers }
    );
  }

  getPlantERP(plant: string) {
    return this.http.get<PlantERPConfig[]>(
      this.gs.baseURL + "api/MasterData/GetPlantERP/" + plant
    );
  }

  getPlantCarriers(plantId: number) {
    return this.http.get<CarrierConfigs[]>(
      this.gs.baseURL + "api/MasterData/GetPlantCarriers/" + plantId
    );
  }
  getCarrierServices(carrierCode: string) {
    return this.http.get<CarrierService[]>(
      this.gs.baseURL + "api/MasterData/GetCarrierServices/" + carrierCode
    );
  }

  getCompanyCode() {
    return this.http.get<CompanyCode[]>(
      this.gs.baseURL + "api/MasterData/GetCompanyCode"
    );
  }

  getUserProfileValues() {
    return this.http.get<string[]>(
      this.gs.baseURL + "api/MasterData/GetUserProfileValues"
    );
  }

  getUOMList() {
    return this.http.get<any>(this.gs.baseURL + "api/MasterData/GetExportUOM");
  }
  
  getDropDownBindData(columnTextFiled:string,columnValueFiled:string,tableName:string){

    return this.http.get<DropDownValue[]>(
      this.gs.baseURL + "GetDropDownValue/" + columnTextFiled + "/"+ columnValueFiled + "/" + tableName);
  }
  getReport(){
    const headers = new HttpHeaders({ "Content-Type": "application/json" });
    return this.http.get<any> (this.gs.baseURL + "GetReport" ,{ headers: headers });
  }

  getLicenceMasterData() {
    return this.http.get<any>(
      this.gs.baseURL + "api/MasterData/GetLicenceMasterData"
    );
  }

  getOriginCriterion() {
    return this.http.get<any>(
      this.gs.baseURL + "api/MasterData/GetOriginCriterion"
    );
  }

  getDefinationValue(code: string) {
    return this.http.get<any>(
      this.gs.baseURL + "api/MasterData/GetDefinationValue/" + code
    );
  }
  getInternationalShipDetailForm(
    carrierIntegrationID: string,
    carrierCode: string
  ): Promise<InternationalShipDetailForm[]> {
    return this.http
      .get<InternationalShipDetailForm[]>(
        this.gs.baseURL + "api/InternationalForm",
        {
          params: {
            CarrierIntegrationID: carrierIntegrationID,
            CarrierCode: carrierCode,
          },
        }
      )
      .toPromise();
  }

  getEflowClient(id: number) {
    return this.http.get<eFlowClient>(this.gs.baseURL + "api/EFlow/" + id);
  }

  unsubscribeEflowClient(data: eFlowClient, id: number) {
    const headers = new HttpHeaders({ "Content-Type": "application/json" });
    return this.http.patch(this.gs.baseURL + "api/EFlow/" + id, data);
  }

  deleteEflowClient(id: number) {
    return this.http.delete(this.gs.baseURL + "api/EFlow/" + id);
  }

  saveEflowClient(data: eFlowClient) {
    const headers = new HttpHeaders({ "Content-Type": "application/json" });
    return this.http.post<any>(this.gs.baseURL + "api/EFlow", data, {
      headers: headers,
    });
  }

  getDeliveryDoc(date) {
    return this.http.get<any>(
      this.gs.baseURL + "api/MasterData/GetDeliveryDoc/" + date
    );
  }

  getReviewShipmentData(shippingNo) {
    return this.http.get<any>(this.gs.baseURL + "ReviewShipment/" + shippingNo);
  }
}
