import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-accessorial-rate-key',
  templateUrl: './accessorial-rate-key.component.html',
  styleUrls: ['./accessorial-rate-key.component.scss']
})
export class AccessorialRateKeyComponent implements OnInit {

  columns = [
    "",
    "",
    "ID",
    "Plant ID",
    "Code",
    "Discription"
  ];
  showAddpopup = true;

  save(){}
  router: any;
  svcService: any;
  data: any;

  constructor() { }

  ngOnInit(): void {
    this.svcService.get().subscribe((data: any) => {
      this.data = data;
    });
  }
  edit(id: string) {
    this.router.navigate(["create-ship-via-code-configuration/" + id]);
  }

}
