var url = "http://localhost:83/";
var ResponseText;
var responseMsg;
var errorMsg;
exports.printLabel = function (script) {
    SendPrintMessage(script);
}
exports.GetCommunicatorDetails = function() {
    AjaxCommunicatorRequest(RecieveMessageWithResponse, url.toString() + "ProcessShipperCommunicator.GetCommunicatorDetails".length, "POST", "ProcessShipperCommunicator.GetCommunicatorDetails", '', '', false);
}
exports.getAllPrinters = function(){
    var ptrs = SendMessage("SPrint.Getprinters", "yes", "yes");
    return ptrs;
}

exports.getScaleWeight = function(comport, budrate, parity, databits, stopbit, pid, vid, type){
    var s = SendMessage("SPort.GetWeight" + "<~~!~~paramseperator~~!~~>" + comport + "<~~!~~paramseperator~~!~~>" + budrate + "<~~!~~paramseperator~~!~~>" + parity + "<~~!~~paramseperator~~!~~>" + databits + "<~~!~~paramseperator~~!~~>" + stopbit + "<~~!~~paramseperator~~!~~>" + pid + "<~~!~~paramseperator~~!~~>" + vid + "<~~!~~paramseperator~~!~~>" + type, "yes", "no");
    return s;
}
function SendPrintMessage(Command) {
  try {
    SendMessage(Command, "No", "No");
  } catch (ex) {
    alert("er" + ex.message);
  }
}

function SendMessage(
  Command,
  ResponseRequired,
  ResponseRequiredWithErrorAlert
) {
  try {
    if (
      ResponseRequired.toUpperCase() == "YES" &&
      ResponseRequiredWithErrorAlert.toUpperCase() != "YES"
    ) {
      AjaxCommunicatorRequest(
        RecieveMessageWithResponse,
        url.toString() + Command.length,
        "POST",
        Command,
        "",
        "",
        true
      );
    } else if (
      ResponseRequired.toUpperCase() == "YES" &&
      ResponseRequiredWithErrorAlert.toUpperCase() == "YES"
    ) {
      AjaxCommunicatorRequest(
        RecieveMessageWithResponseAndShowAlert,
        url.toString() + Command.length,
        "POST",
        Command,
        "",
        "",
        true
      );
    } else {
      AjaxCommunicatorRequest(
        RecieveMessageOnly,
        url.toString() + Command.length,
        "POST",
        Command,
        "",
        "",
        true
      );
    }
    if (
      ResponseRequired.toUpperCase() == "YES" ||
      ResponseRequiredWithErrorAlert.toUpperCase() == "YES"
    ) {
      return responseMsg;
    }
  } catch (errSendMsg) {
    alert("Error in SendMessage method - > " + errSendMsg.message);
  }
}
function RecieveMessageWithResponseAndShowAlert() {
  try {
    var respMSG = ResponseText.split("<~~!~~paramseperator~~!~~>");
    if (respMSG[0] != "Successfully Data Received") {
      alert(respMSG[0]);
    } else {
      if (respMSG[1] != "") {
        responseMsg = respMSG[1];
      }
      if (respMSG[2] != "") {
        alert(respMSG[2]);
        errorMsg = respMSG[2];
      }
    }
  } catch (respError) {
    alert(
      "Error in ProcessShipperCommunicator Error Message : " + respError.message
    );
  }
}
