function RecieveMessageWithResponse() {
  try {
    var respMSG = ResponseText.split("<~~!~~paramseperator~~!~~>");
    if (respMSG[0] != "Successfully Data Received") {
      alert(respMSG[0]);
    } else {
      if (respMSG[1] != "") {
        if (respMSG[1] != "") {
          var MSG = respMSG[1].toString().split("#");
          if (MSG[0] != "CommunicatorServiceExists") {
            alert(MSG[0]);
          }
          if (MSG[1] != "") {
            document.getElementById("MacAddress").value = MSG[1].toString();
          }
        }
      }
    }
  } catch (respError) {
    alert(
      "Error in ProcessShipperCommunicator Error Message : " + respError.message
    );
  }
}
function AjaxCommunicatorRequest(
  ReadyHandler,
  URL,
  Method,
  Params,
  QueryString,
  HttpHeaders,
  ErrorAlertRequired
) {
  try {
    if (URL == null) {
      alert("Request URL is Empty");
    } else {
      if (url != "http://localhost:83/") {
        alert(
          "There are some addon/plugins installed with your browser which are redirecting to some other pages. Please remove those addon/plugins."
        );
      }

      //if ('XDomainRequest' in window && window.XDomainRequest !== null) {// code for IE7+, Firefox, Chrome, Opera, Safari
      //    alert('XDomainRequest');
      //}

      var XMLHttpRequest = require("xhr2");
      var xmlhttp = new XMLHttpRequest();

      //An anonymous function is assigned to the event indicator.
      xmlhttp.onreadystatechange = function () {
        //200 status means ok, otherwise some error code is returned, 404 for example
        //The 4 state means for the response is ready and sent by the server.
        if (xmlhttp.readyState == 4 && xmlhttp.status == 200) {
          ResponseText = xmlhttp.responseText; //get text data in the response
          var ResponseXML = xmlhttp.responseXML; //get xml data in the response
          var ResponseHeaderJSON = xmlhttp.getResponseHeader("Content-Type"); // Extract Data in http header
          var ResponseHeaders = xmlhttp.getAllResponseHeaders(); //Get a string containing all http headers returned by server

          // Make all the results available in tha ReadyHandler via prototyping.
          ReadyHandler.prototype.ResponseText = ResponseText;
          ReadyHandler.prototype.ResponseHeaderJSON = ResponseHeaderJSON;
          ReadyHandler.prototype.ResponseXML = ResponseXML;
          ReadyHandler.prototype.ResponseHeaders = ResponseHeaders;
          // Execute function passed as ReadyHandelr
          ReadyHandler();
        }
      };

      //If querystring is provided Attach it to the url
      if (QueryString != "") {
        var QueryStringData = "";
        for (QueryStringAttribute in QueryString) {
          QueryStringData =
            QueryStringAttribute +
            "=" +
            QueryString[QueryStringAttribute] +
            "&" +
            QueryStringData;
        }
        QueryStringData = QueryStringData.substring(
          0,
          QueryStringData.lastIndexOf("&")
        );
        URL = URL + "?" + escape(QueryStringData); //Here is where the query string ia attached to the request url.
      }

      //POST or GET URL of the script to execute.true for asynchronous (false for synchronous).
      xmlhttp.open(Method, URL, false);
      xmlhttp.setRequestHeader(
        "Content-type",
        "application/x-www-form-urlencoded"
      );

      if (HttpHeaders != "") {
        var HttpHeadersData = "";
        for (HttpHeaderName in HttpHeaders) {
          xmlhttp.setRequestHeader(HttpHeaderName, HttpHeaders[HttpHeaderName]); // Here the custom headers are added
        }
      }

      //Post data provided then assemble it into single string to be posted to server
      if (Params != "") {
        var ParamsData = "";
        for (var ParamName in Params) {
          ParamsData = ParamName + "=" + Params[ParamName] + "&" + ParamsData;
        }
        ParamsData = ParamsData.substring(0, ParamsData.lastIndexOf("&"));
      }
      xmlhttp.send(Params); //Send the request with the post data
      //alert('http sent');
    }
  } catch (err21) {
    if (ErrorAlertRequired) {
      alert(
        "Connection can not be established with ProcessShipperCommunicator Service. \r\n Please check health of Service. \r\n" +
          err21.message
      );
    }
  }
}
