import { ChangeDetectorRef, Component, ElementRef, HostListener, ViewChild, OnInit, AfterViewInit, Renderer2 } from '@angular/core';
import { PlatformLocation } from '@angular/common';
import { MessageService } from 'primeng/api';
import { ConfirmDialog } from 'primeng/confirmdialog';
import { ConfirmationService } from 'primeng/api';
import { Subject, Subscription } from 'rxjs';
import { Router } from '@angular/router';
import { PrimeNGConfig } from 'primeng/api';
import { DataService } from './services/data.service';
import { GlobalService } from './services/global.service';
import { LoaderService } from './services/loader.service';

declare function checkBrowserType(): boolean;

declare var $: any;

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnInit, AfterViewInit {
  isLoading: Subject<boolean> = this.loader.isLoading;

  title = 'Process Shipper Modernization';
  abc;
  sideBarWidth = 48;
  types = [];
  operators = [];
  body: HTMLElement;
  menuCollapsed = true;
  isSettingsPageDirty = false;
  showChatTabs = false;
  footerData:any;

  private subscription: Subscription = new Subscription();
  @ViewChild(ConfirmDialog) cd: ConfirmDialog;
  @ViewChild('firstElem') firstElem: ElementRef;
  showNoResults: boolean = false;



  @HostListener('document:scroll', ['$event']) scrollHandler(event) {
    let openedContextMenu = document.getElementById('openedContextMenu');
    if (openedContextMenu) {
      openedContextMenu.style.visibility = 'hidden';
    }
  }

  // @HostListener('document:keydown', ['$event']) onShortCutKeyPressed(event: KeyboardEvent) {
  //   const e = <KeyboardEvent>event;
  //   console.log(e);
  // }

  constructor(
    private loader: LoaderService,
    public dataService: DataService,
    location: PlatformLocation,
    public gs: GlobalService,
    private toasterService: MessageService,
    private cs: ConfirmationService,
    private cdr: ChangeDetectorRef,
    //private utilService: UtilService,

    private router: Router,
    private primengConfig: PrimeNGConfig,
    private renderer: Renderer2,
  ) {



    const appHeight = () => {
      const doc = document.documentElement;
      const docWidth = doc.clientWidth;
      doc.style.setProperty('--marginpaddingacross', `12px`);
      if (docWidth < 1715) {
        this.gs.buttonCount = 3;
      } else {
        this.gs.buttonCount = 5;
      }
      doc.style.setProperty('--app-height', `${window.innerHeight}px`);
    }
    window.addEventListener('resize', appHeight);
    appHeight();
  }

  ngOnInit() {



    this.subscription.add(
      this.dataService.sidebarToggleObs.subscribe(res => {
        if (res) {
          this.sideBarWidth = 200;
          this.menuCollapsed = false;
        } else if (res == false) {
          this.sideBarWidth = 48;
          this.menuCollapsed = true;
        }
      })
    );
    this.dataService.footerObs.subscribe(data => {
      this.footerData = data;
    });
    // Added to disable chromium from opening files when dropped onto browser
    window.addEventListener("dragover", e => {
      e && e.preventDefault();
    }, false);
    window.addEventListener("drop", e => {
      e && e.preventDefault();
    }, false);
  }


  ngOnDestroy() {
    if (this.renderer) {
      this.renderer.destroy();
    }
    if (this.subscription) {
      this.subscription.unsubscribe();
    }
  }

  ngAfterViewInit() {

  }
}
