import { Component, OnInit } from "@angular/core";
import { configurationService } from "../../../services/configuration-service";
import { ScaleConfigModel } from "src/app/models/printerConfiguration.model";

@Component({
  selector: "app-scale-setup",
  templateUrl: "./scale-setup.component.html",
  styleUrls: ["./scale-setup.component.scss"],
})
export class ScaleSetupComponent implements OnInit {
  scaleType = [
    { textField: "USB", textValue: "USB" },
    { textField: "SERIAL", textValue: "SERIAL" },
    { textField: "NONE", textValue: "NONE" },
    { textField: "CUBISCAN", textValue: "CUBISCAN" },
  ];
  comPort = [
    { textField: "COM1" },
    { textField: "COM2" },
    { textField: "COM3" },
    { textField: "COM4" },
    { textField: "COM5" },
    { textField: "COM6" },
    { textField: "COM7" },
    { textField: "COM8" },
    { textField: "COM9" },
    { textField: "COM10" },
  ];
  baudRates = [
    { textField: "300" },
    { textField: "600" },
    { textField: "1200" },
    { textField: "2400" },
    { textField: "4800" },
    { textField: "9600" },
    { textField: "14400" },
    { textField: "28800" },
    { textField: "36000" },
    { textField: "115000" },
  ];
  parity = [{ textField: "None" }, { textField: "Even" }, { textField: "Odd" }];
  dataBit = [{ textField: "7" }, { textField: "8" }, { textField: "9" }];
  stopBits = [{ textField: "1" }, { textField: "2" }, { textField: "3" }];

  scaleConfigModel!: ScaleConfigModel;

  constructor(private configurationService: configurationService) {}

  ngOnInit(): void {
    this.configurationService.getSacleConfigurationValue().subscribe((data) => {
      this.scaleConfigModel = data;
    });
  }
  save() {
    this.configurationService
      .saveScaleConfiguration(this.scaleConfigModel)
      .subscribe((response) => {
        console.log(response);
      });
  }
}
